// field
select,
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	border-radius: 3px;
	box-sizing: border-box;
	display: block;
	font-size:17px;
	width: 100%;
	background: #f7f7f7;
	border: 1px solid transparent;
	line-height: 1.75;
	padding: 7px 15px;
	&:focus {
		color:#000;
		box-shadow: none;
		outline: none;
		border:1px solid #000;
	}
}
input[type=button],
input[type=reset],
input[type=submit] {
    padding: 10px 30px;
    width: auto;
	font-size:17px;

}
textarea {
	width: 100%;
	padding: 15px 20px;
}
input[type="checkbox"] {
	display: inline;
}

input,
textarea{
	&::-webkit-input-placeholder {
		color: #cacaca;
	}
	&::-ms-input-placeholder {
		color: #c3c3c3;
	}
	&::-moz-placeholder {
		color: #a3a2a2;
	}
}


// contact form 7

.wpcf7 span.ajax-loader {
	left: 50%;
	position: absolute;
	top: 15px;
}

.wpcf7 p {
	position: relative;
}

.wpcf7 {
	input[type="button"],
	input[type="reset"],
	input[type="submit"] {
		margin-top: 20px;
		padding: 15px 30px;
		width: auto;
	}
}
.wpcf7 input[type="button"],
.wpcf7 input[type="reset"],
.wpcf7 input[type="submit"] {
	border:none;
}





