@import "./mixin";

.gutenify-slider {
	.swiper-pagination {
		@include custom-swiper-pagination-style;
		.swiper-pagination-bullet {
			@include custom-swiper-pagination-bullet-style;
		}
	}

	.swiper-button-next,
	.swiper-button-prev {
		@include swiper-next-prev-style;
	}
	.swiper-button-next {
		@include swiper-next-style;
	}
	.swiper-button-prev {
		@include swiper-prev-style;
	}
	.swiper-button-next::after,
	.swiper-button-prev::after {
		@include swiper-next-prev-icon-style;
	}
	.gutenify-gallery-carousel-item::before {
		@include slider-overlay-style;
	}
}

//style two
.slider-style-two,
.gutenify-section-layout-2{
	.swiper-button-next,
	.swiper-button-prev {
		border-radius: 100%;
		background: #0000006e;
		color: #fff;
		height: 60px;
		width: 60px;
	}
	.swiper-button-next {
		right: 5%;
	}
	.swiper-button-prev {
		left: 5%;
	}
	.swiper-pagination {
		.swiper-pagination-bullet {
			height: 5px;
			width: 35px;
			border-radius: 0;
		}
	}
}

//style three

.gutenify-section-layout-3{
	.swiper-button-next,
	.swiper-button-prev {
		height: 50px;
		width: 50px;
		top: 125px;
	}
	.swiper-button-next {
		right: 170px;
	}
	.swiper-button-prev {
		left: inherit;
		right: 225px;
	}

}
//style four

.gutenify-section-layout-4{
	.swiper-button-next,
	.swiper-button-prev {
	    height: 60px;
		width: 60px;
		top: 10%;
	}
	.swiper-button-next {
		right: 100px;
	}
	.swiper-button-prev {
		left: inherit;
   		 right: 165px;
	}
	.swiper-pagination {
		top: 45%;
		left: 3%;
		text-align: left;
		width: auto;
		.swiper-pagination-bullet {
			height: 33px;
			width: 10px;
			border-radius: 0;
			display: block;
			margin: 15px;
		}
	}
}

//style Five

.gutenify-section-layout-5 {
	.swiper-button-next,
	.swiper-button-prev {
		height: 60px;
		width: 60px;
		top: inherit;
		bottom: 10%;
	}
	.swiper-button-next {
		right: 100px;
	}
	.swiper-button-prev {
		left: inherit;
   		 right: 165px;
	}
}

// Responsive

@media screen and (max-width: 1023px) {
	.gutenify-slider {
		.swiper-button-next,
		.swiper-button-prev {
			@include swiper-next-prev-tablet-style;
			height: 35px;
			width: 35px;
		}
		.swiper-button-next::after,
		.swiper-button-prev::after {
			@include swiper-next-prev-after-tablet-style;
		}
	}
	.gutenify-section-layout-2 {
		.swiper-button-next {
			right:2%;
		}
		.swiper-button-prev {
			left:2%;
		}
	}

	.gutenify-section-layout-3,
	.gutenify-section-layout-4,
	.gutenify-section-layout-5{
		.swiper-button-next,
		.swiper-button-prev {
			height: 35px;
			width: 35px;
			top:100px;
		}
		.swiper-button-next {
			right: 3px;
		}
		.swiper-button-prev {
			left: inherit;
			right: 40px;
		}
	}
	.gutenify-section-layout-5{
		.swiper-button-next,
		.swiper-button-prev {
			top:inherit;
			bottom:20px;
			z-index: 999;
		}
	}
}
@media screen and (max-width: 767px) {
	.gutenify-slider {
		.swiper-button-next,
		.swiper-button-prev {
			@include swiper-next-prev-mobile-style;
		}
		.swiper-button-next::after,
		.swiper-button-prev::after {
			@include swiper-next-prev-after-mobile-style;
		}
		.swiper-pagination {
			@include custom-swiper-pagination-mobile-style;
			.swiper-pagination-bullet {
				@include custom-swiper-pagination-bullet-mobile-style;
			}
		}
	}
	.slider-style-two .swiper-pagination,
	.gutenify-section-layout-2 {
		.swiper-pagination-bullet {
			height: 5px;
			width: 35px;
			border-radius: 0;
		}
	}
	.gutenify-section-layout-4 .swiper-pagination {
		.swiper-pagination-bullet {
			margin:10px 5px;
		}
	}
}
