@import "./mixin";

.gutenify-team-block-wrapper {
	.gutenify-team-section {
		@include team-member-block-wrapper-style;
		.gutenify-team-item {
			padding: 40px;
		}
	}
	&.gutenify-team-col-1 {
		.gutenify-team-section {
			grid-template-columns: 1fr;
		}
	}

	&.gutenify-team-col-2 {
		.gutenify-team-section {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&.gutenify-team-col-3 {
		.gutenify-team-section {
			grid-template-columns: repeat(3, 1fr);
		}
	}
	&.gutenify-team-col-4 {
		.gutenify-team-section {
			grid-template-columns: repeat(4, 1fr);
		}
	}
	&.gutenify-team-col-5 {
		.gutenify-team-section {
			grid-template-columns: repeat(5, 1fr);
		}
	}
	&.gutenify-team-col-6 {
		.gutenify-team-section {
			grid-template-columns: repeat(6, 1fr);
		}
	}
	&.gutenify-team-col-7 {
		.gutenify-team-section {
			grid-template-columns: repeat(7, 1fr);
		}
	}
	&.gutenify-team-col-8 {
		.gutenify-team-section {
			grid-template-columns: repeat(8, 1fr);
		}
	}
}

@media screen and (max-width: 1023px) {
	.wp-block-gutenify-team.gutenify-team-block-wrapper .gutenify-team-section {
		@include wp-block-gutenify-team-member-media;
	}
}
