@import "./mixin";

.gutenify-service-block-wrapper {
	.gutenify-service-block {
		@include service-block-wrapper-style;
	}
	&.gutenify-service-col-1 {
		.gutenify-service-block {
			grid-template-columns: 1fr;
		}
	}

	&.gutenify-service-col-2 {
		.gutenify-service-block {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&.gutenify-service-col-3 {
		.gutenify-service-block {
			grid-template-columns: repeat(3, 1fr);
		}
	}

	&.gutenify-service-col-4 {
		.gutenify-service-block {
			grid-template-columns: repeat(4, 1fr);
		}
	}

	&.gutenify-service-col-5 {
		.gutenify-service-block {
			grid-template-columns: repeat(5, 1fr);
		}
	}
	&.gutenify-service-col-6 {
		.gutenify-service-block {
			grid-template-columns: repeat(6, 1fr);
		}
	}
	&.gutenify-service-col-7 {
		.gutenify-service-block {
			grid-template-columns: repeat(7, 1fr);
		}
	}
	&.gutenify-service-col-8 {
		.gutenify-service-block {
			grid-template-columns: repeat(8, 1fr);
		}
	}

}

@media screen and (max-width: 1023px) {
	.wp-block-gutenify-service.gutenify-service-block-wrapper .gutenify-service-block {
		@include wp-block-gutenify-info-box;
	}
}
@media screen and (max-width: 768px) {
	.wp-block-gutenify-service.gutenify-service-section .gutenify-service-block {
		.gutenify-info-box-section {
			padding: 30px;
		}
	}
}

.gutenify--service--hide-button .wp-block-gutenify-button {
	display: none;
}
