@import "./mixin";
@import "./dynamic";

.gutenify-button-link.wp-block-button__link {
	@include gutenify-button-link-style;
}

.wp-block-gutenify-button {
	display: inline-block;
	margin-bottom: 0;
	margin-top: 0;
	&.has-custom-font-size {
		.gutenify-button-section {
			display: inline-block;
			font-size: inherit;
		}
	}
}
