//wp block gutenify container style here//
// $prefix: gutenify-content-toggle-item-;
.gutenify-block-content-toggle-item-wrapper {
	border-color: #f0f0f0;
	border-style: solid;
	border-width: 1px;
	margin-bottom: 10px;
}

.gutenify-content-toggle-item-wrapper {
	background: #fff;
}

.gutenify-content-toggle-item-closed {
	.gutenify-content-toggle-item-header {
		.dashicons-arrow-up-alt2 {
			display: none;
		}
		.dashicons-arrow-down-alt2 {
			display: block;
		}
	}

	.gutenify-content-toggle-item-content {
		display: none;
	}
}

.gutenify-content-toggle-item-open {
	.gutenify-content-toggle-item-header {
		cursor: pointer;
		.dashicons-arrow-up-alt2 {
			display: block;
		}
		.dashicons-arrow-down-alt2 {
			display: none;
		}
	}

	.gutenify-content-toggle-item-content {
		display: block;
		padding: 20px 30px 15px;
	}
}

.gutenify-content-toggle-item-header {
	background: #f4f9fc;
	cursor: pointer;
	display: flex;
	justify-content: space-between;
	padding: 15px 25px;
	h4 {
		margin: 0;
	}
}

.gutenify-content-toggle-item-heading {
	margin: 0;
}

.gutenify-content-toggle-item-handle {
	background-color: transparent;
	border: 0;
	display: inline-block;
	padding: 0;
}

[data-type="gutenify/content-toggle-item"] {
	.block-list-appender {
		margin-top: 10px;
		position: relative;
		width: 100%;
	}
}
