@import "./mixin";

.wp-block-gutenify-team {
	.gutenify-team-item {
		.gutenify-team-member-section {
			@include gutenify-team-member-block-grid-wrapper-style;
			.wp-block-image {
				grid-area: team-member-thumb;
				margin: -40px -40px 30px;
			}
			.gutenify-team-member-block-bio {
				grid-area: team-member-bio;
				margin: 0 0 15px;
				&:empty {
					display: block;
					height: 0;
					line-height: 0;
					margin: 0;
					padding: 0;
				}
			}
			.gutenify-team-member-block-name {
				grid-area: team-member-full-name;
				margin: 0 0 10px;
				&:empty {
					display: block;
					height: 0;
					line-height: 0;
					margin: 0;
					padding: 0;
				}
			}
			.gutenify-team-member-block-degination {
				color: inherit;
				font-weight: 500;
				grid-area: team-member-degination;
				margin: 0 0 15px;
				&:empty {
					display: block;
					height: 0;
					line-height: 0;
					margin: 0;
					padding: 0;
				}
			}
			.wp-block-social-links {
				font-size: 16px;
				gap: 5px;
				grid-area: team-member-social-links;
				margin: 0;
				&:empty {
					display: block;
					height: 0;
					line-height: 0;
					margin: 0;
					padding: 0;
				}
				.wp-social-link {
					a {
						padding: 7px;
					}
				}
			}
		}
	}
	&.gutenify-team-layout-2 {
		.gutenify-team-item {
			padding: 30px;
			.gutenify-team-member-section {
				@include gutenify-team-member-block-grid-wrapper-layout-two-style;
				.wp-block-image {
					grid-area: team-member-thumb;
					margin: -30px 0 -30px -30px;
				}
			}
		}
	}
	&.gutenify-team-layout-3 {
		.gutenify-team-item {
			padding: 30px;
			.gutenify-team-member-section {
				@include gutenify-team-member-block-grid-wrapper-layout-three-style;
				.wp-block-image {
					grid-area: team-member-thumb;
					margin: -30px -30px -30px 0;
				}
			}
		}
	}

	&.gutenify-team-layout-4 .gutenify-team-member-section {
	    align-content: space-around;
		display: grid;
		grid-template-areas:
			"team-member-thumb team-member-thumb"
			"team-member-social-links team-member-full-name"
			"team-member-social-links team-member-degination"
			"team-member-social-links team-member-bio";
		grid-template-columns: auto;
		grid-template-rows: auto;
		column-gap: 20px;
		.wp-block-social-links {
			gap: 10px;
			font-size: inherit;
		}
	}
	&.gutenify-team-layout-5 .gutenify-team-member-section {
	    align-content: space-around;
		display: grid;
		grid-template-areas:
			"team-member-thumb"
			"team-member-social-links"
			"team-member-full-name"
			"team-member-degination"
			"team-member-bio";
			grid-template-columns: auto;
			grid-template-rows: auto;
			column-gap: 20px;
		.wp-block-social-links {
			gap: 10px;
			font-size: inherit;
			margin-bottom: 25px;
		}
	}
}
.gutenify-team-member-block-degination {
    font-size: 1rem;
}
