@import "./mixin";

.wp-block-gutenify-info-box {
	@include gutenify-info-box-block-grid-wrapper-style;
	&.gutenify-info-box-layout-2 {
		@include gutenify-info-box-block-grid-wrapper-layout-two-style;
		.wp-block-gutenify-icon {
			display: table;
		}
	}
	&.gutenify-info-box-layout-3 {
		@include gutenify-info-box-block-grid-wrapper-layout-three-style;
		.wp-block-gutenify-icon {
			display: table;
			text-align: center;
		}
	}

	&.gutenify-info-box-layout-6 {
		text-align: center;
		.gutenify-icon-wrapper {
			margin-bottom: 15px;
		}
	}
	.wp-block-gutenify-icon {
		grid-area: info-box-icon;
		margin: 0 0 10px;
		&:empty {
			display: block;
			height: 0;
			line-height: 0;
			margin: 0;
			padding: 0;
		}

	}
	.gutenify-admin-info-box-block-title {
		grid-area: info-box-title;
		margin: 0 0 10px;
		&:empty {
			display: block;
			height: 0;
			line-height: 0;
			margin: 0;
			padding: 0;
		}
	}
	.gutenify-admin-info-box-block-subtitle {
		color: inherit;
		font-weight: 500;
		grid-area: info-box-subtitle;
		margin: 0 0 10px;
		&:empty {
			display: block;
			height: 0;
			line-height: 0;
			margin: 0;
			padding: 0;
		}
	}
	.gutenify-admin-info-box-block-content {
		grid-area: info-box-content;
		margin: 0 0 10px;
		&:empty {
			display: block;
			height: 0;
			line-height: 0;
			margin: 0;
			padding: 0;
		}
	}
	[data-type="gutenify/buttons"] {
		grid-area: info-box-button;
		margin: 0 0 10px;
	}
}
.gutenify-info-box-layout-4 {
	text-align: center;
	.gutenify-icon-wrapper {
		background: #f3f8ff;
		padding: 20px;
		border-radius: 100%;
		margin-bottom: 10px;
		box-sizing: border-box;
	}
}
.gutenify-info-box-layout-5 {
	text-align: center;
	.gutenify-icon-wrapper {
		background: #f3f8ff;
		padding: 15px;
		margin-bottom: 25px;
		transform: rotate(45deg);
		height: 70px;
		width: 70px;
		box-sizing: border-box;
		i {
			transform: rotate(-45deg);
		}
	}
}
