@import "./mixin";

.wp-block-gutenify-star-rating {

	&.is-content-justification-left {
		text-align: left;
	}
	&.is-content-justification-right {
		text-align: right;
	}
	&.is-content-justification-center {
		text-align: center;
	}
	.gutenify-star-rating-section {
		display: inline-flex;
		gap: 5px;
	}
	.gutenify-star-rating-empty{
		color: grey;
	}
}
