@import "./mixin";

.gutenify-testimonials-block-wrapper {
	.gutenify-testimonials-block {
		@include testimonial-block-wrapper-style;
	}
	&.gutenify-testimonials-col-1 {
		.gutenify-testimonials-block {
			grid-template-columns: 1fr;
		}
	}

	&.gutenify-testimonials-col-2 {
		.gutenify-testimonials-block {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&.gutenify-testimonials-col-3 {
		.gutenify-testimonials-block {
			grid-template-columns: repeat(3, 1fr);
		}
	}

	&.gutenify-testimonials-col-4 {
		.gutenify-testimonials-block {
			grid-template-columns: repeat(4, 1fr);
		}
	}

	&.gutenify-testimonials-col-5 {
		.gutenify-testimonials-block {
			grid-template-columns: repeat(5, 1fr);
		}
	}
	&.gutenify-testimonials-col-6 {
		.gutenify-testimonials-block {
			grid-template-columns: repeat(6, 1fr);
		}
	}
}

@media screen and (max-width: 1023px) {
	.gutenify-testimonials-block-wrapper {
		.gutenify-testimonial-section {
			.gutenify-testimonials-block {
				@include gutenify-testimonials-block;
			}
		}
	}
}
